.search-box {
   width: 300px;
   padding: 10px 20px;
   outline: none;
   border-radius: 10px;
   border: 5px solid #95dada;
   transition: .5s;
   background-color: #eee;
}
.search-box:focus {
   width: 400px;
   background-color: transparent;
   border-width: 2px;
   color: white;
}